import React, { useEffect, useState, useRef } from 'react';
import { Line } from 'react-chartjs-2';
import 'chart.js/auto';

const API_BASE = 'http://localhost:5000/api';

function App() {
  const [status, setStatus] = useState('Loading...');
  const [sensorValue, setSensorValue] = useState(null);
  const [history, setHistory] = useState([]);
  const [lastUpdated, setLastUpdated] = useState(null);
  const intervalRef = useRef();

  // Fetch latest status and history
  const fetchData = async () => {
    try {
      const statusRes = await fetch(`${API_BASE}/status`);
      const statusData = await statusRes.json();
      setStatus(statusData.status);
      setSensorValue(statusData.sensor_value);
      setLastUpdated(statusData.timestamp);

      const historyRes = await fetch(`${API_BASE}/history`);
      const historyData = await historyRes.json();
      setHistory(historyData);
    } catch (err) {
      setStatus('Error');
    }
  };

  useEffect(() => {
    fetchData();
    intervalRef.current = setInterval(fetchData, 2000);
    return () => clearInterval(intervalRef.current);
  }, []);

  // Prepare data for Chart.js
  const chartData = {
    labels: history.map(item => new Date(item.timestamp).toLocaleTimeString()),
    datasets: [
      {
        label: 'Sensor Value',
        data: history.map(item => item.sensor_value),
        fill: false,
        borderColor: '#007bff',
        tension: 0.2,
      },
    ],
  };

  const chartOptions = {
    responsive: true,
    plugins: {
      legend: { display: false },
    },
    scales: {
      x: { title: { display: true, text: 'Time' } },
      y: { title: { display: true, text: 'Sensor Value' } },
    },
  };

  return (
    <div style={{ maxWidth: 600, margin: '40px auto', fontFamily: 'Arial, sans-serif' }}>
      <h1 style={{ textAlign: 'center' }}>Leak Detection Dashboard</h1>
      <div style={{
        background: status === 'Leak' ? '#ffcccc' : '#e6ffe6',
        color: status === 'Leak' ? '#b30000' : '#006600',
        padding: 20,
        borderRadius: 8,
        textAlign: 'center',
        fontSize: 24,
        marginBottom: 24,
        border: '1px solid #ccc',
      }}>
        Status: <b>{status === 'Leak' ? 'Leak Detected' : status === 'Normal' ? 'Normal' : status}</b>
        {sensorValue !== null && (
          <span style={{ fontSize: 16, marginLeft: 16 }}>(Value: {sensorValue})</span>
        )}
        {lastUpdated && (
          <div style={{ fontSize: 12, marginTop: 8 }}>Last updated: {new Date(lastUpdated).toLocaleString()}</div>
        )}
      </div>
      <div style={{ background: '#fff', padding: 16, borderRadius: 8, boxShadow: '0 2px 8px #eee' }}>
        <h2 style={{ textAlign: 'center', fontSize: 20 }}>Sensor Value (Last 20)</h2>
        <Line data={chartData} options={chartOptions} />
      </div>
    </div>
  );
}

export default App; 